.container {
  flex-direction: column;
  justify-content: center;
  height: 50%;
  width: 50%;
  background-color: rgb(53, 52, 52);
  align-items: center;
  margin: auto;
  margin-bottom: 30px;
}

#students {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 1px solid #ddd;
  width: 100%;
}

#students td, #students th {
  border: 1px solid ;
  padding: 10px;
  color: white;
}

#students tr:hover {background-color: rgb(75, 73, 73);}

h4 {
  font-size: 25px;
  color: white;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-top: 30px;
  padding: 20px;
}

.file {
  margin-top: 20px;
  margin-bottom: 10px;
  color: #65c768;
  align-items: center;
  justify-content: center;
  display: flex;
}

.title {
  margin-top: 50px;
  margin-bottom: 5px;
  color: white;
  font-size: 20px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.text1 {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text2 {
  height: 30px;
  width: 200px;
}

.button1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.btn1 {
  height: 40px;
  width: 90px;
  background-color: rgb(105, 103, 103);
  color: white;
  border: 2px solid rgb(42, 42, 43);
  font-size: 20px;
  margin-bottom: 30px;
  cursor: pointer;
}

.container1 {
  flex-direction: column;
  justify-content: center;
  height: 50%;
  width: 50%;
  background-color: rgb(53, 52, 52);
  align-items: center;
  margin: auto;
}

h5 {
  padding: 20px;
  font-size: 25px;
  margin-bottom: 50px;
  color: white;
  align-items: center;
  justify-content: center;
  display: flex;
}

#table{
width: 105%;
}

#table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  color: white;
  
}

.card {
  height: 100vh;
  display: flex;
}


.image {
  margin-top: -100px;
  justify-content: center;
}

.img {
  height: 100px;
  width: 100px;
}

.button2 {
  margin-left: 100px;
  margin-top: -40px;
}

.btn2 {
  height: 30px;
  width: 50px;
  background-color: rgb(105, 103, 103);
  color: white;
  border: 2px solid rgb(42, 42, 43);
  font-size: 20px;
  cursor: pointer;
}

.button3 {
  margin-left: 100px;
  display: flex;
  margin-top: -30px;
}

.btn3 {
  height: 30px;
  width: 80px;
  background-color: rgb(105, 103, 103);
  color: white;
  border: 2px solid rgb(42, 42, 43);
  font-size: 20px;
  cursor: pointer;
}

@media screen and (max-width: 300px) {
  .container {
    width: 30%;
    height: 30%;
    justify-content: center;
    align-items: center;
  }
  .container1 {
    width: 30%;
    height: 30%;
    justify-content: center;
    align-items: center;
  }
}
